<template>
<v-container fluid>
  <v-row v-if="reports?.length === 0" justify="center" align="center" style="min-height: 300px;">
    <v-card-title class="no-comment">
      
    </v-card-title>
  </v-row>
  <v-row v-else>
    <v-col v-for="(report, index) in reports" :key="index" :cols="12" :sm="12">
      <report-card
      :complaint-id="report.complaintId"
      :source-id="String(report.sourceId)"
      :target="String(report.target)"
      :post-title="String(report.postTitle)"
      :post-id="String(report.postId)"
      :post-type="report.type"
      :context="report.context">

      </report-card>
    </v-col>
  </v-row>
</v-container>
</template>


<script>
import ReportCard from "@/views/repport/reportCard";
import {reportReq} from "@/api/report";
import {actionFailed, actionPostFailed} from "@/components/ActionState";

let testReport =
  {
    complaintId: "1",
    sourceId: "1",
    target: "1",
    postTitle: "",
    postId: "1",
    type: "buy",
    context:`# api

 2 , 1 

##  /complaint

-  : \`/api/complaint\`.
-  : \`get\`.
-  : , admin 
-  :
  - complaints: , ,
    -  complaintId: , id
    -  sourceId: , id.
    -  target: , id.
    -  postTitle: , .
    -  postId: , id
    -  postType: , , 1, 2.
    -  context: , 

## 

-  : \`/api/complaint\`.
-  : \`post\`.
-  :
  - complaintId: , id
-  :  success  message `
  };

let testReports = [];
for (let i = 0; i < 20; i++) {
  testReports.push(testReport)
}

export default {
  name: "reportView",
  components: {ReportCard},
  data() {
    return {
      reports: []
    }
  },
  methods: {
    loadReport() {
      let vm = this;
      reportReq('get',{

      }).then(res => {
        if (res.data.success) {
          vm.reports = res.data.data.complaints;
        } else {
          actionPostFailed(res);
        }
      }).catch(err => {
        vm.reports = testReports;
        actionFailed();
      })
    }
  },
  mounted() {
    this.loadReport();
  }
}
</script>

<style scoped>
.no-comment {
  font-size: xx-large;
  color: gray;
  opacity: 0.5;
}
</style>
